import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Location, LocationStrategy, PathLocationStrategy, PopStateEvent } from '@angular/common';
import 'rxjs/add/operator/filter';
import { NavbarComponent } from './components/navbar/navbar.component';
import { Router, NavigationEnd, NavigationStart, ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import PerfectScrollbar from 'perfect-scrollbar';
import { AUTH_COOKIE, AuthService, DataService } from './shared';
import { CookieService } from 'ngx-cookie-service';


declare const $: any;

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
    private _router: Subscription;
    private lastPoppedUrl: string;
    private yScrollStack: number[] = [];
    private isAuthenticated: boolean = false;

    @ViewChild(NavbarComponent) navbar: NavbarComponent;

    constructor(public location: Location, private authService: AuthService, private dataService: DataService,
        private router: Router, private route: ActivatedRoute, private cookieService: CookieService) { }

    ngOnInit() {
        $.material.init();
        this.checkAuthentication();
        const elemMainPanel = <HTMLElement>document.querySelector('.main-panel');
        const elemSidebar = <HTMLElement>document.querySelector('.sidebar .sidebar-wrapper');

        this.dataService.authenticationMessage.subscribe(z => this.checkAuthentication());

        this.location.subscribe((ev: PopStateEvent) => {
            this.lastPoppedUrl = ev.url;
        });

        let isLoginPage = this.route.snapshot.url.find((u: any) => {
            return u.path.toLowerCase() === 'login';
        }) || false;

        this.router.events.subscribe((event) => {
            if (event instanceof NavigationEnd) {
                if (event.url === '/table-list') {
                    this.checkAuthentication();
                }
                if (event.url === '/reports') {
                    this.checkAuthentication();
                }
                if (event.url === '/login') {
                    document.querySelector('body').classList.add('login');
                } else {
                    document.querySelector('body').classList.remove('login');
                }
            }
        });

        if (!this.isAuthenticated && !isLoginPage) {
            this.router.navigate(['login']);
        }

        this._router = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event: NavigationEnd) => {
            // elemMainPanel.scrollTop = 0;
            // elemSidebar.scrollTop = 0;
        });
        // if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
        //     let ps = new PerfectScrollbar(elemMainPanel);
        //     ps = new PerfectScrollbar(elemSidebar);
        // }
    }
    ngAfterViewInit() {
        // this.runOnRouteChange();
    }
    // isMaps(path) {
    //     var titlee = this.location.prepareExternalUrl(this.location.path());
    //     titlee = titlee.slice(1);
    //     if (path == titlee) {
    //         return false;
    //     }
    //     else {
    //         return true;
    //     }
    // }
    // runOnRouteChange(): void {
    //     if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
    //         const elemMainPanel = <HTMLElement>document.querySelector('.main-panel');
    //         // const ps = new PerfectScrollbar(elemMainPanel);
    //         // ps.update();
    //     }
    // }
    // isMac(): boolean {
    //     let bool = false;
    //     if (navigator.platform.toUpperCase().indexOf('MAC') >= 0 || navigator.platform.toUpperCase().indexOf('IPAD') >= 0) {
    //         bool = true;
    //     }
    //     return bool;
    // }

    checkAuthentication() {
        let authCookie = this.cookieService.get("authCookie");

        // let authCookie = this.cookieProvider.getCookie(AUTH_COOKIE);

        if (authCookie) {
            // this.authService.setUser(authCookie);
            this.isAuthenticated = true;
        }
    }
}



